body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #006400; /* Dark green felt */
    color: #fff;
}

.header {
    position: relative;
    background-color: #8B0000; /* Dark red */
    padding: 20px;
    text-align: center;
}

.header nav a {
    color: gold;
    margin: 0 15px;
    text-decoration: none;
}

.game {
    text-align: center;
    padding: 20px;
}

.card {
    display: inline-block;
    width: 80px;
    height: 120px;
    background-color: white;
    color: black;
    border: 1px solid #000;
    margin: 5px;
    line-height: 120px;
    font-size: 24px;
    position: relative;
}

.choices button,
.result button {
    padding: 10px 20px;
    margin: 10px;
    font-size: 16px;
}

.correct {
    color: green;
}

.incorrect {
    color: red;
}

.progress {
    margin-top: 20px;
}

embed, iframe {
    border: 1px solid #ccc;
    border-radius: 8px;
    margin-top: 20px;
}

.strategy-chart {
    text-align: center;
    padding: 20px;
}

.strategy-chart img {
    display: block;
    margin: 0 auto 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
}

.completion {
    text-align: center;
    margin-top: 50px;
}

.completion h2 {
    color: green;
    font-size: 2rem;
}

.completion p {
    font-size: 1.2rem;
    margin: 10px 0;
}

.completion button {
    padding: 10px 20px;
    font-size: 1rem;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.completion button:hover {
    background-color: #45a049;
}

.credit {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 0.9rem;
}

.credit a {
    color: gold;
    text-decoration: none;
    font-weight: bold;
}

.credit a:hover {
    text-decoration: underline;
}

